export default {

    //存储数据
    state: {
        token: '',
        role_ids: [],
        userinfo: {
            name: '',
            mobile: ''
        },
        
    },
    //调用方法
    mutations: {
        settoken(state, val) {
            state.token = val
        },
        setuser(state, val) {
            state.userinfo.name = val.name;
            state.userinfo.mobile = val.mobile;
            state.role_ids = val.role_ids;
        },
        clearuser(state) {
            state.userinfo.name = "";
            state.userinfo.mobile = "";
            state.role_ids = [];
        },
    },
    actions: {
        
    }
}