<template>
    <div id="app" v-if="isAlive">
        <router-view />
    </div>
</template>
<script>

export default {
    name: "App",
    data() {
        return {
            isAlive: true
        };
    },
    provide() {
        //在父组件中创建属性
        return {
            routerRefresh: this.routerRefresh
        };
    },
    created() {
        this.fetchItems();
    },
    methods: {
        routerRefresh() {
            this.isAlive = false;
            this.$nextTick(() => {
                this.isAlive = true;
            });
        },
        fetchItems() {
            this.$store.dispatch("org/fetchItems");
        }
    }
};
</script>
<style lang="scss">
#app {
    height: 100vh;
    .el-input.is-disabled .el-input__inner,
    .el-textarea.is-disabled .el-textarea__inner {
        color: black;
    }
}
#nprogress .spinner {
    right: 15px !important;
    top: 15px !important;
}
</style>
