export default {

    //存储数据
    state: {
        isCollapse: false,
        currentMenu: null,
        catch_components: [],
        tabsList: [
            {
                Path: '/',
                Name: 'home',
                Label: '主页',
            }
        ]
    },
    //调用方法
    mutations: {
        collapseMenu(state) {
            state.isCollapse = !state.isCollapse
        },
        selectMenuAside(state, val) {
            if (val.Name === 'home') {
                state.currentMenu = null
            } else {
                state.currentMenu = val
                //如果等于-1说明tabsList不存在那么插入，否则什么都不做
                let result = state.tabsList.findIndex(item => item.Name === val.Name)
                result === -1 ? state.tabsList.push(val) : ''
            }
        },
        //选择标签 选择面包屑
        selectMenu(state, val) {
            if (val.Name === 'home') {
                state.currentMenu = null
            } else {
                state.currentMenu = val
                //如果等于-1说明tabsList不存在那么插入，否则什么都不做
                let result = state.tabsList.findIndex(item => item.Name === val.Name)
                result === -1 ? state.tabsList.push(val) : ''

                // let result2 = state.catch_components.findIndex(item => item === val.Name)
                // result2 === -1 ? state.catch_components.push(val.Name):'';
                // console.log('selectMenu',state.catch_components)
            }
        },
        //关闭标签
        closeTab(state, val) {
            let result = state.tabsList.findIndex(item => item.Name === val.Name)
            state.tabsList.splice(result, 1)

            // let cache = state.catch_components;
            // for (let i = 0; i < cache.length; i++) {
            //     if (cache[i] === val.Name) {
            //         state.catch_components = cache.splice(1, 1);
            //     }
            // }
        },
        closeTabAside(state, val){
            let cache = state.catch_components;
            for (let i = 0; i < cache.length; i++) {
                if (cache[i] === val.Name) {
                    state.catch_components.splice(i, 1);
                }
            }
            console.log('closeTabAside',state.catch_components)
        },
    },
    actions: {}
}