import Vue from 'vue'
import VueRouter from 'vue-router'

const routerPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return routerPush.call(this, location).catch(error => error)
}

Vue.use(VueRouter)

// 完整路由代码
const router = new VueRouter({
  routes: [

    {
      path: '/login',
      name: 'login', //登录
      component: () => import('@/views/Login/Login'),
    },
    {
      path: '/404',
      component: () => import('@/views/Login/Notfound'),
      hidden: true
    },
    {
      path: '/',
      component: () => import('@/views/Main'),
      redirect: '',
      children: [
        {
          path: '/',
          name: 'home', //首页ed
          component: () => import('@/views/Home/Home'),
        },
        {
          path: '/systemAdmin',
          name: 'systemAdmin', //设置-管理员
          component: () => import('@/views/limit/system_admin'),
        },
        {
          path: '/limit',
          name: 'limit', //权限
          component: () => import('@/views/limit/limit'),
        },
        {
          path: '/role',
          name: 'role', //权限-角色
          component: () => import('@/views/limit/role'),
        },
         {
          path: '/recordList',
          name: 'recordList', //操作日志
          component: () => import('@/views/limit/recordList'),
        },
      ]
    },
    {
      path: '*',
      redirect: '/404',
      name: 'Page404',
      hidden: true
    }
  ]
})



export default router