// });

// Vue.use(VueAMap);


// 进度条
import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css' // progress bar style
NProgress.configure({
  easing: 'ease',
  speed: 1000,
  showSpinner: true,
})
// console.log('default29',process.env)

// 路由拦截
router.beforeEach((to, from, next) => {
  NProgress.start();
  const mytoken = VueCookies.get('mytoken');
  // console.log('token', mytoken);
  // const mytoken = document.cookie;
  if (to.name === 'login') {
    next()
  } else {
    if (mytoken) {
      next()
    } else {
      VueCookies.remove('mytoken');
      localStorage.clear();
      next({
        path: "/login"
      })
    }
  }
  NProgress.done()
})

router.afterEach(() => {
  NProgress.done()
})


// 第三方包
import ElementUI from 'element-ui'
import _double from 'lodash'
ElementUI.Dialog.props.closeOnClickModal.default = false;
ElementUI.Dialog.props.closeOnPressEscape.default = false;
Vue.use(ElementUI)
Vue.prototype.apiSever = axios.apiSever
Vue.prototype.URl = URl.url
Vue.prototype.IMGURL = URl.imgurl
Vue.prototype.$_double = _double;
Vue.config.productionTip = false
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')