import service from 'axios'
import qs from 'qs';
import {
  Message
} from "element-ui";
import VueCookies from 'vue-cookies'

const apiSever = {
  get(url,successCallback, errorCallback,){
      _requestSever(url, null,successCallback, errorCallback);
  },
  post(url,data, successCallback, errorCallback) {
      // console.log(data)
      _requestSever(url,data,successCallback, errorCallback);
  },
};

function _requestSever(url, data,successCallback, errorCallback) {
  let request = () => {
      let p = data ? service.post(_dealUrl(url), data) : service.get(_dealUrl(url));
      p.then((response) => {
          successCallback(response);
      }).catch((error) => {
          // console.log(error)
          if(errorCallback){
            errorCallback(error);
          }
         
      })
  };
  request();
}
function _dealUrl(url) {
  if (!url) {
      console.log("url未定义", "api");
      return;
  }
  return  url;
}

// 添加请求拦截器
service.interceptors.request.use(
  config => {
    const mytoken = VueCookies.get('mytoken')
    // 在发送请求之前
    // if (config.method != 'get') {
    //   config.data = qs.stringify(config.data);
    // };
    config.headers['Authorization'] = 'Bearer ' + mytoken;
    return config
  },
  error => {
    console.log('错误请求', error);
    return Promise.reject(error);
  }
)

//响应拦截
service.interceptors.response.use(
  response => {
    let res = {}
    res.status = response.status
    res.data = response.data
    if (res.status === 200) {
      return Promise.resolve(res);
    }
  },
  error => {
    console.log('错误响应', error);
    if (error.response.status === 401) {
      // 开发过程中暂时注释
      VueCookies.remove("mytoken");
      localStorage.clear();
      // location.reload();
    }else{
      Message.error(error.response.data)
    }
    
    if (error.response.data.message) {
      Message.error(error.response.data.message)
    }
    return Promise.reject(error);
  }
)


export default{
  service,
  apiSever
} 